.aboutInfo__item {
  padding-bottom: 2rem;
  position: relative;

  width: 60%;

  .title,
  .items {
    display: inline-block;
  }

  .title {
    font-weight: 400;
    font-size: 2rem;
  }
  .items {
    display: inline-flex;
    gap: 1rem;
    font-weight: 400;
    position: absolute;
    right: 0;

    @media (max-width: 600px) {
      position: relative;
      display: flex;
      flex-direction: column;
      gap: 0.5rem;

      p {
        margin-top: 0;
        margin-bottom: 0;
      }
    }
  }
  .content {
    margin-top: 3rem;
    overflow-wrap: break-word;
  }
}
