@import "../../styles/shared";
.textButton_container {
  margin-top: 2rem;
  .button {
    font-size: 2.2rem;
    background-color: $button-color-paper;
    padding: 1rem 2.5rem;
    border: 1px solid $button-color-paper;
    border-radius: 8px;
    display: inline-block;
    color: $reverse-font-color;

    &:hover {
      color: $base-font-color;
      background-color: $reverse-font-color;
    }
  }
  @media only screen and (max-width: 768px) {
    .button {
      font-size: 1.8rem;
    }
  }
}
