@import "../../styles/shared";

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  background-color: $reverse-font-color;
  padding: 1rem 10rem;
  border-top: 1px solid $base-font-color;
  .footer__container {
    display: flex;
    justify-content: space-between;

    ul {
      padding-left: 0;
    }
    li {
      text-decoration: none;
      list-style: none;
    }

    .footer__col1 {
      display: flex;
      align-items: center;
    }
    @media only screen and (max-width: 768px) {
      .container {
        flex-direction: column;
        gap: 0rem;
        & > div {
          margin-top: 5rem;
        }
      }
      .footer__col1 .para {
        max-width: 100%;
      }
    }
  }

  .copyright {
    background-color: $reverse-font-color;
    text-align: left;
    padding: 1rem 0;
    .para {
      margin-left: 0;
    }

    @media only screen and (max-width: 768px) {
      .container {
        div {
          margin-top: 0;
        }
      }
    }
  }
}
