@import "../../styles/shared";
header {
  position: fixed;
  z-index: 999;

  .navbar__icon__home {
    position: fixed;
    top: 4rem;
    left: 4rem;
    width: 3rem;
    height: 3rem;

    button {
      color: $base-font-color;
      padding: 5px;
      border: 1px solid currentColor;
      border-radius: 9999px;

      svg,
      path {
        fill: $base-font-color !important;
      }
    }
  }

  .navbar__icon__menu {
    position: fixed;
    top: 4rem;
    right: 4rem;
    width: 3rem;
    height: 3rem;
    button {
      color: $base-font-color;
      padding: 1px;
      border: 1px solid currentColor;
      border-radius: 9999px;

      svg,
      path {
        stroke: $base-font-color !important;
      }
    }
  }

  nav {
    position: fixed;
    top: 8rem;
    right: 4rem;
    width: 3rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 70%;
    transition: height 0.15s ease-in-out;
    overflow: hidden;

    .navbar__nav-list {
      position: relative;
      transform: rotate(90deg) translate3d(50%, 0, 0);
      display: flex;

      .navbar__nav-link {
        position: relative;
        padding: 5px;
        margin-right: 2rem;
        overflow: hidden;
      }

      .navbar__nav-link:hover {
        color: $reverse-font-color;
      }

      .navbar__nav-link::before {
        transform: translateX(105%);
      }

      .navbar__nav-link::after {
        transform: translateX(-105%);
      }

      .navbar__nav-link::before,
      .navbar__nav-link::after {
        z-index: -1;
        display: block;
        position: absolute;
        left: 0;
        top: 0;
        transition: transform 0.3s ease-in-out;
        background-color: rgba($base-font-color, 0.8);
        content: "";
        height: 100%;
        width: 100%;
      }

      .navbar__nav-link:hover::after,
      .navbar__nav-link:hover::before {
        transform: translateX(0);
      }

      .navbar__nav-link--active {
        color: $reverse-font-color;
        &::after,
        &::before {
          transform: translateX(0);
        }
      }
    }

    .navbar__nav-icons {
      color: $base-font-color;
      display: flex;
      flex-direction: column;

      svg {
        fill: $base-font-color !important;
      }
    }
  }

  nav.hidden {
    height: 0;
  }
}
